<template>
  <div class="bg-gray">
    <!-- 基础用法 -->
    <demo-block title="基础用法" :card="true">
      <gw-cell title="提示弹框" is-link @click="show1 = true"></gw-cell>
      <gw-cell title="提示弹框 (无标题)" is-link @click="show2 = true "></gw-cell>
      <gw-cell title="确认弹框" is-link @click="show3 = true"></gw-cell>
    </demo-block>

    <!-- 自定义用法 -->
    <demo-block title="自定义用法" :card="true">
      <gw-cell title="表单弹框" is-link @click="show4 = true"></gw-cell>
      <gw-cell title="图片弹框" is-link @click="show5 = true"></gw-cell>
    </demo-block>

    <!-- 异步关闭 -->
    <demo-block title="异步关闭" :card="true">
      <gw-cell title="异步关闭" is-link @click="show6 = true"></gw-cell>
    </demo-block>

    <GwModal
      v-if="show1"
      v-model:show="show1"
      :title="'标题'"
      :showCancelButton="false"
      :confirmButtonText="'我知道了'"
      @cancel="show1 = false"
      @confirm="handleConfirm"
    >
    这是一段佳话
    </GwModal>

    <GwModal
      v-if="show2"
      v-model:show="show2"
      @cancel="show2 = false"
      @confirm="handleConfirm"
    >
    这是一段佳话
    </GwModal>

    <GwModal
      v-if="show3"
      :title="'标题'"
      v-model:show="show3"
      @cancel="show3 = false"
      @confirm="handleConfirm"
    >
    这是一段佳话
    </GwModal>

    <GwModal
      v-if="show4"
      :title="'表单'"
      v-model:show="show4"
      @cancel="show4 = false"
      @confirm="handleConfirm"
    >
    <input class="ipt" placeholder="请输入内容" />
    </GwModal>

    <GwModal
      v-if="show5"
      :title="'表单'"
      v-model:show="show5"
      @cancel="show5 = false"
      @confirm="handleConfirm"
    >
      <Icon :size="100" :name="demoImage" />
    </GwModal>

    <GwModal
      v-if="show6"
      :title="'异步'"
      :loading="loading"
      v-model:show="show6"
      @cancel="show6 = false"
      @confirm="handleConfirm('loading')"
    >
    这是一段佳话
    </GwModal>


    
  </div>
</template>

<script>
import { defineComponent, reactive, toRefs } from 'vue'
import { CellGroup, Cell, Dialog, Icon } from 'gw-vue-ui'

export default defineComponent({
  components: {
    GwCellGroup: CellGroup,
    GwCell: Cell,
    GwModal: Dialog,
    Icon
  },
  setup () {
    const state = reactive({
      show1: false,
      show2: false,
      show3: false,
      show4: false,
      show5: false,
      show6: false,
      loading: false
    })
    const handleConfirm = (type)=> {
      console.log('confirm')
      state.show1 = false
      state.show2 = false
      state.show3 = false
      state.show4 = false
      state.show5 = false
      if (type === 'loading') {
        state.loading = true
        setTimeout(() => {
        state.loading = false
        state.show6 = false
        }, 3000)
      }
    }
    return {
      handleConfirm,
      demoImage: 'https://test-fshows-public.oss-cn-hangzhou.aliyuncs.com/huifu-account-img/2021-05-11/1620727755013-RJMddsN6jY4YDP4HDGFrRwizWpNFi2rZ.jpeg',
      ...toRefs(state)
    }
  }
})
</script>

